import React, { Component } from 'react';
import * as _ from 'lodash';
import './App.scss';
import { BrowserRouter as Router, Switch, Route, Link } from 'react-router-dom';
import Rus from './russia.jpg';
import Bel from './belorussia.jpg';
import Eng from './england.jpg';
import Data from './codejam5.json';

const Main = (props) => {
  const header = {
    ru: 'Главная',
    by: 'Галоуная',
    en: 'Main'
  }
  const { lang } = props;
  return () => {return <h1>{header[lang]}</h1>};
}
const Search = (props) => {
  const header = {
    ru: 'Поиск',
    by: 'Пошук',
    en: 'Search'
  }
  const { lang } = props;
  return () => {return <h1>{header[lang]}</h1>};
}

const Menu = {
  ru: {
    main:'Главная',
    search: 'Поиск',
    brand: 'Белорусские писатели'
  },
  by: {
    main:'Галоуная',
    search: 'Пошук',
    brand: 'Беларускія пісьменнікі'
  },
  en: {
    main:'Main',
    search: 'Search',
    brand: 'Belarusian writers'
  }  
}

class App extends Component {

  state = {
    lang: 'ru'
  }

  switchLanguage = (event) => {
    const key = event.target.id;
    this.setState({lang: event.target.id});
    console.log(_.keys(Data[key].autors));
    console.log(_.keys(Data[key].interface.mainMenu));
    console.log(_.values(Data[key].autors));
  }

  render() {
    const {main, search, brand} = Menu[this.state.lang];
    const mainPage = Main(this.state);
    const searchPage = Search(this.state);
    return (
      <Router basename="/codejam5" >
        <div className="wrapper">
          <nav className="navbar navbar-expand-md navbar-light bg-light"> 
            <p className="navbar-brand" >{brand}</p>        
            <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
              <span className="navbar-toggler-icon"></span>
            </button>
            <div className="collapse navbar-collapse" id="navbarSupportedContent">
              <ul className="navbar-nav mr-auto">
                <li className="nav-item">
                  <Link to={'/home'} className="nav-link">{main}</Link>
                </li>
                <li className="nav-item">
                  <Link to={'/search'} className="nav-link">{search}</Link>
                </li>
              </ul>
              <div onClick={this.switchLanguage}>
                  <button ><img id='ru' width='30' src={Rus} alt='russian language'/></button>
                  <button ><img id='by' width='30' src={Bel} alt='russian language'/></button>
                  <button ><img id='en' width='30' src={Eng} alt='russian language'/></button>
              </div>                           
            </div>
          </nav>
          <Switch>
              <Route exact path='/' component={mainPage}/>
              <Route exact path='/home' component={mainPage}/>
              <Route exact path='/search' component={searchPage} />
          </Switch>
        </div>
      </Router>
    );
  }
}
export default App;
